// 退出登录
export function logoutClient(data){
  const res = axios.post('/api/mall/user/logoutClient',data);
  return new Promise((resolve,reject)=>{
    res
      .then((result)=>{
        if(result.status===200){
          return result.data;
        }else{
          reject(result.status)
        }
      })
      .then((json)=>{
        if(json.code===0){
          resolve();
        }else{
          reject(json.message);
        }
      })
      .catch((e)=>{
        reject(e.toString())
      })
  })
}



